import { NextRequest, NextResponse } from 'next/server'

const PYTHON_BACKEND_URL = process.env.PYTHON_BACKEND_URL || 'http://localhost:8080'

export async function GET(request: NextRequest) {
  try {
    const searchParams = request.nextUrl.searchParams
    const player_id = searchParams.get('player_id')
    const season = searchParams.get('season') || '2024-25'

    if (!player_id) {
      return NextResponse.json(
        { success: false, error: 'player_id parameter is required' },
        { status: 400 }
      )
    }

    const url = `${PYTHON_BACKEND_URL}/api/player-season-stats?player_id=${player_id}&season=${season}`
    
    const response = await fetch(url, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    })

    if (!response.ok) {
      const errorData = await response.json()
      return NextResponse.json(
        { success: false, error: errorData.error || 'Failed to fetch player season stats' },
        { status: response.status }
      )
    }

    const data = await response.json()
    return NextResponse.json(data)
  } catch (error) {
    console.error('Error proxying player season stats request:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}
